import "./assets/css/index.css";
import Navbar from "./components/navbar/NavBar";
import DailyReport from "./pages/dailyreport/DailyReport";
import Home from "./pages/home/Home";
import ProfitReport from "./pages/profitReport/ProfitReport";
import {
  DailyAppend,
  ProductAppend,
  CustomerAppend,
} from "./components/forms/Form";

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function App() {
  return (
    <div>
      <Router>
        <div className="App">
          <Navbar />
          <Switch>
            <Route exact path="/" component={Home}></Route>
            <Route path="/dailyreport" component={DailyReport}></Route>
            <Route path="/profitreport" component={ProfitReport}></Route>
            <Route path="/dailyappend" component={DailyAppend}></Route>
            <Route path="/productappend" component={ProductAppend}></Route>
            <Route path="/customerappend" component={CustomerAppend}></Route>
            <Route path="*" component={Home}></Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
