import { ProductSummary } from "./Summary";
function ProductsSold({ items }) {
  let selling = 0;
  let profit = 0;

  for (var i = 0; i < items.length; ++i) {
    selling += Number(items[i]["Total"]);
    profit += Number(items[i]["Puchase Rate"]);
  }

  return (
    <div>
      <div>
        <ProductSummary selling={selling} profit={profit} />
      </div>
      <div className="flex flex-col h-screen">
        <div className="flex-grow overflow-auto">
          <table className="min-w-max w-full table-auto">
            <thead>
              <tr className="text-black-900 uppercase text-xl leading-normal">
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">Date</th>
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">
                  Category
                </th>
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">Type</th>
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">
                  Product
                </th>
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">
                  Quantity
                </th>
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">
                  Sold Rate
                </th>
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">
                  Purchase Rate
                </th>
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">
                  Profit
                </th>
                <th className="sticky bg-yellow-200 top-0 px-6 py-3 ">
                  Bill No
                </th>
              </tr>
            </thead>
            <tbody className="divide-y">
              {items.map((item, index) => (
                <tr key={index} className={"bg-white-200"}>
                  <td className="px-6 py-4 border text-center">
                    {item["Date"]}
                  </td>
                  <td className="px-6 py-4 border text-center">
                    {item["Category"]}
                  </td>
                  <td className="px-6 py-4 border text-center">
                    {item["Type"]}
                  </td>
                  <td className="px-6 py-4 border text-center">
                    {item["Product"]}
                  </td>
                  <td className="px-6 py-4 border text-center">
                    {item["Quantity"]}
                  </td>
                  <td className="px-6 py-4 border text-center">
                    {item["Sold Rate"]}
                  </td>
                  <td className="px-6 py-4 border text-center">
                    {item["Puchase Rate"]}{" "}
                  </td>
                  <td className="px-6 py-4 text-center">{item["Profit"]}</td>
                  <td className="px-6 py-4 text-center">{item["Bill No"]}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default ProductsSold;
