import { DailySummary } from "./Summary";
function RoyalDaily({ items }) {
  let credit = 0;
  let debit = 0;
  let pending = 0;

  for (var i = 0; i < items.length; ++i) {
    credit += Number(items[i]["Cash In"]);
    debit += Number(items[i]["Cash Out"]);
    pending += Number(items[i]["Pending"]);
  }

  return (
    <div>
      <div>
        <DailySummary credit={credit} debit={debit} pending={pending} />
      </div>
      <div className="flex flex-col h-screen">
        <div className="flex-grow overflow-auto">
          <table className="min-w-max w-full table-auto">
            <thead>
              <tr className="text-black-900 uppercase text-xl leading-normal">
                <th className="sticky bg-yellow-200  top-0 px-6 py-3 ">Date</th>
                <th className="sticky bg-yellow-200  top-0 px-6 py-3 ">
                  Credit
                </th>
                <th className="sticky bg-yellow-200  top-0 px-6 py-3 ">
                  Debit
                </th>
                <th className="sticky bg-yellow-200  top-0 px-6 py-3 ">
                  Pending
                </th>
                <th className="sticky bg-yellow-200  text-left top-0 px-6 py-3 ">
                  Category
                </th>
                <th className="sticky bg-yellow-200  text-left top-0 px-6 py-3 ">
                  Description
                </th>
                <th className="sticky bg-yellow-200  top-0 px-6 py-3 ">
                  Bill No
                </th>
              </tr>
            </thead>
            <tbody className="divide-y">
              {items.map((item, index) => (
                <tr key={index} className="text-lg bg-white-200">
                  <td className="px-6 py-4 text-center">{item["Date"]}</td>
                  <td className="px-6 py-4 border  text-center">
                    {item["Cash In"]}
                  </td>
                  <td className="px-6 py-4 border  text-center">
                    {item["Cash Out"]}{" "}
                  </td>
                  <td className="px-6 py-4 border  text-center">
                    {item["Pending"]}
                  </td>
                  <td className="px-6 py-4 border  text-left">
                    {item["Category"]}
                  </td>
                  <td className="px-6 py-4 border  text-left">
                    {item["Description"]}
                  </td>
                  <td className="px-6 py-4 text-center">{item["Bill No."]}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default RoyalDaily;
