import { useState, useEffect } from "react";
import { BASE_URL, URLS } from "../url";
import { Spinner } from "../../components/spinner/Spinner";
import { ProductSheet } from "../../components/sheets/Sheets";

const ProfitReport = () => {
  const [data, setData] = useState();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const URL = BASE_URL + URLS.report + "/Products Sold";

  useEffect(() => {
    fetch(URL)
      .then((response) => {
        if (response.ok) return response.json();
        throw response;
      })
      .then((data) => {
        setData(JSON.parse(data.todayhisab));
      })
      .catch((error) => {
        console.error("Error Fetching data: ", error);
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);
  return <div>{loading ? <Spinner /> : <ProductSheet items={data} />}</div>;
};

export default ProfitReport;
